
import { computed, defineComponent, SetupContext } from 'vue'
import { faTimes } from '@fortawesome/free-solid-svg-icons'
import { faVrCardboard } from '@fortawesome/free-solid-svg-icons'

export default defineComponent({
  props: {
    isModalActive: {
      type: Boolean,
      required: true
    },
    courseImage: {
      type: String,
      required: true
    },
    connectingDeviceName: {
      type: String,
      required: true
    },
    courseTitle: {
      type: String,
      required: true
    },
    instructorName: {
      type: String,
      required: true
    }
  },
  emits: ['set-modal-active'],
  setup(props, context: SetupContext) {
    const setModalActive = (value: boolean) => {
      context.emit('set-modal-active', value)
    }

    const closeIcon = computed(() => faTimes)
    const vrIcon = computed(() => faVrCardboard)

    return {
      closeIcon,
      vrIcon,
      setModalActive
    }
  }
})
