<template>
  <div class="modal-wrap" @click.self="setModalActive(false)" v-show="isModalActive">
    <transition>
      <div class="modal-content" v-show="isModalActive">
        <button class="close-button" @click="setModalActive(false)">
          <font-awesome-icon :icon="closeIcon" class="close-icon" />
        </button>
        <div class="image-wrap">
          <img src="@/assets/vr.png" alt="" />
        </div>
        <h2 class="vr-text">{{ $t('VR-device.headset') }}</h2>
        <p class="device-name">
          <font-awesome-icon :icon="vrIcon" class="vr-icon" />
          <span> → {{ connectingDeviceName }} </span>
        </p>
        <div class="course-info">
          <img :src="courseImage" alt="" class="course-image" />
          <div class="info">
            <p class="course-title">{{ courseTitle }}</p>
            <p class="instructor-name">{{ instructorName }}</p>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, SetupContext } from 'vue'
import { faTimes } from '@fortawesome/free-solid-svg-icons'
import { faVrCardboard } from '@fortawesome/free-solid-svg-icons'

export default defineComponent({
  props: {
    isModalActive: {
      type: Boolean,
      required: true
    },
    courseImage: {
      type: String,
      required: true
    },
    connectingDeviceName: {
      type: String,
      required: true
    },
    courseTitle: {
      type: String,
      required: true
    },
    instructorName: {
      type: String,
      required: true
    }
  },
  emits: ['set-modal-active'],
  setup(props, context: SetupContext) {
    const setModalActive = (value: boolean) => {
      context.emit('set-modal-active', value)
    }

    const closeIcon = computed(() => faTimes)
    const vrIcon = computed(() => faVrCardboard)

    return {
      closeIcon,
      vrIcon,
      setModalActive
    }
  }
})
</script>

