<template>
  <notice title="VRデバイスに接続できませんでした" :noticeActive="isNoticeActive" />
  <div class="wrap" v-show="popupActive" @click.self="popupCancel">
    <div class="content-wrap" :class="{ 'popup-active': isAnimationActive }">
      <h3 class="title">{{ $t('VR-device.connect') }}</h3>
      <ul>
        <li v-for="(device, index) in btDevices" :key="index" @click="deviceConnection(device)">
          <font-awesome-icon :icon="castIcon" class="icon" />
          <span>{{ device.deviceName }}</span>
        </li>
      </ul>
      <div class="cancel" @click="popupCancel">
        <font-awesome-icon :icon="closeIcon" class="icon" />
        <span>{{ $t('VR-device.cancel') }}</span>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, SetupContext, ref, watch } from 'vue'
import { faChromecast } from '@fortawesome/free-brands-svg-icons'
import { faTimes } from '@fortawesome/free-solid-svg-icons'
// import BluetoothDevicesDemoData from '@/callAndroidMethod/BluetoothDevicesDemoData'
import { BluetoothDevice } from '@/store'
import { useStore } from 'vuex'
import Notice from '@/components/Notice.vue'
//@ts-ignore
import findDevice from '@/callAndroidMethod/findDevice'

export default defineComponent({
  components: {
    Notice
  },
  props: {
    popupActive: {
      type: Boolean,
      required: true
    },
    videoData: {
      type: Object
    }
  },
  emits: ['device-link-popup-active', 'set-modal-active'],
  setup(props, context: SetupContext) {
    const store = useStore()

    const castIcon = computed(() => faChromecast)
    const closeIcon = computed(() => faTimes)
    const isAnimationActive = ref(false)
    const isNoticeActive = ref(false)

    const setLoading = (value: boolean) => {
      store.commit('setLoading', value)
    }

    const noticeActive = () => {
      isNoticeActive.value = true
      setTimeout(() => (isNoticeActive.value = false), 1500)
    }

    const btDevices = ref([] as BluetoothDevice[])
    // btDevices.value = BluetoothDevicesDemoData
    btDevices.value = JSON.parse(findDevice)

    const animationActive = () => setTimeout(() => (isAnimationActive.value = true), 10)

    watch(
      () => props.popupActive,
      (value) => {
        if (!value) return
        animationActive()
      }
    )

    const popupCancel = () => {
      context.emit('device-link-popup-active', false)
      isAnimationActive.value = false
    }

    //ToDo ポップアップ選択時の通信

    const deviceConnection = (btDevice: BluetoothDevice) => {
      setLoading(true)
      setTimeout(() => {
        store
          .dispatch('requestSetConnectingBluetoothDevice', {
            device: btDevice
          })
          .then(async () => {
            const isBtResult = await store.dispatch('submitBTDeviceData', {
              videoData: JSON.stringify(props.videoData)
            })
            setLoading(false)
            if (isBtResult === 'true') {
              popupCancel()
              context.emit('set-modal-active', true)
            } else {
              //TODO: falseの処理
              noticeActive()
            }
          })
      }, 100)
    }

    return {
      castIcon,
      closeIcon,
      popupCancel,
      deviceConnection,
      isAnimationActive,
      btDevices,
      isNoticeActive
    }
  }
})
</script>

