  background-color: rgba(0, 0, 0, 50%);
  backdrop-filter: blur(5px);

  $height: 50%;

  .content-wrap {
    position: relative;
    bottom: -$height;
    display: grid;
    grid-template-rows: 2rem 1fr 3rem;
    width: 100%;
    height: $height;
    padding: 1rem;
    color: #eee;
    background-color: #222;

    .title {
      font-size: 1.125rem;
    }

    .icon {
      margin-right: 1rem;
      font-size: 1.25rem;
    }

    ul {
      overflow-x: scroll;

      li {
        display: flex;
        align-items: center;
        padding: 0.75rem 0;
        list-style: none;
        &:active {
          transform: translateY(2px);
        }
      }
    }

    .cancel {
      display: flex;
