import { gql } from '@apollo/client/core'

const takeCourseDetail = gql`
  query TakeCourseDetail($id: uuid, $student_id: uuid) {
    SubscriptionCourses(where: { id: { _eq: $id } }) {
      last
      id
      Course {
        image
        title
        file
        duration
        Instructor {
          icon
          name
          id
        }
        Sessions(order_by: { order: asc }) {
          title
        }
        Exams {
          title
        }
        Reviews(where: { student_id: { _eq: $student_id } }) {
          id
          student_id
        }
        ChatRooms(where: { student_id: { _eq: $student_id } }) {
          id
        }
      }
    }
  }
`

export default takeCourseDetail
