
import { computed, defineComponent, ref } from 'vue'
import { useRouter } from 'vue-router'
import { useQuery, useResult } from '@vue/apollo-composable'
import { useStore } from 'vuex'

import takeCourseDetail from '@/graphql/queries/takeCourseDetail'
import { TakeCourseDetailQuery } from '@/graphql/@types/types'
import { faPlayCircle } from '@fortawesome/free-regular-svg-icons'
import { faPenSquare, faQuestionCircle, faComments, faMedal } from '@fortawesome/free-solid-svg-icons'
// component
import BackButton from '@/components/BackButton.vue'
import CourseImage from '@/components/CourseImage.vue'
import TeacherInfo from '@/components/TeacherInfo.vue'
import Lectures from '@/components/Lectures.vue'
import SkillTest from '@/components/SkillTest.vue'
import DeviceLinkPopup from '@/components/DeviceLinkPopup.vue'
import Modal from '@/components/Modal.vue'

interface SendVRDeviceData {
  subscription_id: string
  url: string
  length: number
  last: number
}

export default defineComponent({
  components: {
    BackButton,
    CourseImage,
    TeacherInfo,
    Lectures,
    SkillTest,
    DeviceLinkPopup,
    Modal
  },
  setup() {
    const { currentRoute } = useRouter()
    const id = currentRoute.value.query.id
    const store = useStore()
    const connectingDeviceName = computed(() => store.state.connectingBluetoothDevice.deviceName)

    const isDeviceLinkPopupActive = ref(false)
    const isModalActive = ref(false)
    const sendVRDeviceData = ref<SendVRDeviceData>()

    const setDeviceLinkPopupActive = (payload: boolean) => {
      isDeviceLinkPopupActive.value = payload
    }
    const setModalActive = (payload: boolean) => {
      isModalActive.value = payload
    }

    const { result, onResult } = useQuery<TakeCourseDetailQuery>(takeCourseDetail, {
      id,
      student_id: store.state.currentUser?.uid
    })
    const subscriptionCourseLast = useResult(result, null, (data) => data.SubscriptionCourses[0].last)
    const subscriptionCourseId = useResult(result, null, (data) => data.SubscriptionCourses[0].id)
    const course = useResult(result, null, (data) => data.SubscriptionCourses[0].Course)
    const instructor = useResult(result, null, (data) => data.SubscriptionCourses[0].Course.Instructor)
    const lectures = useResult(result, null, (data) => data.SubscriptionCourses[0].Course.Sessions)
    const skillTests = useResult(result, null, (data) => data.SubscriptionCourses[0].Course.Exams)
    const review = useResult(result, null, (data) => data.SubscriptionCourses[0].Course.Reviews)
    const chatRoom = useResult(result, null, (data) => data.SubscriptionCourses[0].Course.ChatRooms)

    onResult(() => {
      sendVRDeviceData.value = {
        subscription_id: (subscriptionCourseId?.value as unknown) as string,
        url: course.value?.file as string,
        length: course.value?.duration as number,
        last: subscriptionCourseLast.value as number
      }

      console.log(sendVRDeviceData.value)
    })

    const questionIcon = computed(() => faQuestionCircle)
    const reviewIcon = computed(() => faPenSquare)
    const commentsIcon = computed(() => faComments)
    const playIcon = computed(() => faPlayCircle)
    const medalIcon = computed(() => faMedal)

    return {
      questionIcon,
      reviewIcon,
      commentsIcon,
      playIcon,
      medalIcon,
      subscriptionCourseLast,
      subscriptionCourseId,
      course,
      instructor,
      lectures,
      skillTests,
      review,
      chatRoom,
      isDeviceLinkPopupActive,
      setDeviceLinkPopupActive,
      isModalActive,
      setModalActive,
      connectingDeviceName,
      sendVRDeviceData
    }
  }
})
